import React, { Component } from "react";
import { Modal } from "react-bootstrap";
import "../styling/Styles.css";

class Modalajout extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    return (
      <div className="container">
        <Modal
          {...this.props}
          size="md"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header
            closeButton
            style={{ backgroundColor: "white", color: "#020F64" }}
          >
            <Modal.Title id="contained-modal-title-vcenter">
              <b>Infos Bon de commande</b>
            </Modal.Title>
          </Modal.Header>
          <Modal.Body>
            <p>
              Remise globale : cest la remise globale pour client. Marge Ben :
              marge bnficiaire ralise sur le bon de commande. -le
              remplissage du tableau se fait par la saisie des articles soit
              manuellement ou gnrer la liste des articles par F1. N.B
              :lenregistrement ne peut tre effectu que lorsque tableau
              contient au moins un article. -la suppression du bon de commande
              se fait sur deux tapes :  Annuler  puis  supprimer . -Le
              bouton Ventes reprsente la liste des ventes dun article
              slectionn, cela permet  lutilisateur de consulter lhistorique
              des prix de vente selon le client slectionn dans le bon de
              commande. -le bouton Achats reprsente la liste des achats dun
              article slectionn, cela permet  lutilisateur de consulter
              lhistorique des prix dachats. N.B : le bouton  Remise\TTC 
              permet  lutilisateur de changer directement le montant TTC du
              bon de commande.
            </p>
          </Modal.Body>
          <Modal.Footer></Modal.Footer>
        </Modal>
      </div>
    );
  }
}

export default Modalajout;
